.header-container{width:100%;padding:10px 20px;background-color:#4d4d60;display:flex;align-items:center;justify-content:flex-end}.login-wrapper{display:flex;flex-direction:column;align-items:flex-end}.login-title{color:#fff;font-size:16px;margin-bottom:10px}.login-form{display:flex;align-items:center;gap:10px}.login-input{padding:8px;border:1px solid #ccc;border-radius:4px;font-size:14px}.login-button{background-color:#856825;color:#fff;border:none;padding:8px 12px;border-radius:4px;cursor:pointer;transition:background-color .3s ease}.login-button:hover{background-color:#684f1f}header{display:flex;justify-content:space-between;align-items:center;background-color:#bdb9b9;height:100px}header .menuBar{display:flex;justify-content:center;align-items:center;height:100%;gap:0;width:auto}header .menuBar div{display:inline-flex;align-items:center;justify-content:center;padding:10px;cursor:pointer;transition:background-color .3s ease;height:100%;width:100px}header .menuBar div svg{transition:transform .3s ease}header .menuBar div:hover svg{transform:scale(1.5)}header .menuBar div.active{background-color:gray}header .menuBar div.active svg{transform:scale(1.5)}