.header-container{width:100%;padding:10px 20px;background-color:#4d4d60;display:flex;align-items:center;justify-content:flex-end}.login-wrapper{display:flex;flex-direction:column;align-items:flex-end}.login-title{color:#fff;font-size:16px;margin-bottom:10px}.login-form{display:flex;align-items:center;gap:10px}.login-input{padding:8px;border:1px solid #ccc;border-radius:4px;font-size:14px}.login-button{background-color:#856825;color:#fff;border:none;padding:8px 12px;border-radius:4px;cursor:pointer;transition:background-color .3s ease}.login-button:hover{background-color:#684f1f}h1{font-size:60px;margin-bottom:20px;color:#000}h4{margin-bottom:5px}hr{width:100%}section{height:auto;display:flex;justify-content:flex-start;align-items:center}section .TitleSection{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:97%}section .MenuProjectSection{height:100%;width:100%}